.App {
  text-align: center;
}


.header {
  width: 100%;
  height: 100vh;
  background: url("./img/agung.jpg") no-repeat 50% 50%;
  background-size: cover;
}

.footer {
background-color: #bdd4e7;
background-image: linear-gradient(315deg, #bdd4e7 0%, #8693ab 74%);
width: 100%;
height: 15em;
padding: 2em;
border-top: 1px solid white;
text-align: inital;
}

.footer-col {
  display: inline-block;
}

.footer-social-div {
  margin-top: 1em;
}

.footer-social-icons {
  padding: 0.25em;
}

.fa-facebook-square, .fa-twitter-square, .fa-linkedin-in, .fa-instagram {
  color: #fff;
}

.description {
  background-color: #ffffff;
/* background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%);
border-bottom: 1px solid white; */

}

.main-text {
  font-family: 'Arimo', sans-serif;
  font-size: 32px;
  font-weight: 400;
  text-align: center;
  margin: auto;
  width: 50%;

}

.nagra {
  width: 50%;
    display: inline-block;
    margin-top: 3em;
    margin-bottom: 2em;
}

.nagra-main {
  padding: 1em;
}

.text-col-nagra {
  padding: 0px;
  position: relative;
  height: 100%;
  min-height: 345px;

}

.text-p-nagra {
  padding-left: 2em;
  padding-right: 2em;
  font-family: Arimo;
  font-size: 20px;
}

.small-description {
  text-align: center;
  width: 50%;
  margin: auto;
  padding-top: 5em;
}

.citation {
  font-size: 8px;
}

.kab-buttons{
  padding: 2em;

}


.content {
  width: 94%;
  margin: 4em auto;
  font-size: 20px;
  line-height: 30px;
  text-align: justify;
}

.logo {
  float: left;
  margin: 16px 46px;
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 2px;
  border-radius: 50%;  

}

.logo-img {
  height: 50%;
  width: 50%;
  margin: 0 auto;

}

.mask-logo {
  display: block;
  border-radius: 50%; 
  height: 5em; 
  width: 5em;
}


.nav {
  width: 100%;
  line-height: 60px;
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) )
}

.nav ul {
  line-height: 60px;
  list-style: none;
  background: rgba(0, 0, 0, 0);
  overflow: hidden;
  color: #fff;
  padding: 0;
  text-align: right;
  margin: 0;
  padding-right: 40px;
  transition: 1s;
}

.nav.black ul {
  background: #000;
}

.nav ul li {
  display: inline-block;
  padding: 16px 40px;;
}

.nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}

.menu-icon {
  line-height: 60px;
  width: 100%;
  background: #000;
  text-align: right;
  box-sizing: border-box;
  padding: 15px 24px;
  cursor: pointer;
  color: #fff;
  display: none;
}


@media screen and (min-width:300px) and (max-width:1024px) {
  .nagra {
    width: 90%;
  }

  .text-p-nagra {
    top: 0;
    font-family: Arimo;
    font-size: 20px;
    padding: 0em;
    position: inherit;
  }
}

@media(max-width: 786px) {

  .logo {        
        top: 0;
        margin-top: 16px;
  }

  .nav ul {
        max-height: 0px;
        background: #000;
  }

  .nav.black ul {
        background: #000;
  }

  .showing {
        max-height: 34em;
  }

  .nav ul li {
        box-sizing: border-box;
        width: 100%;
        padding: 24px;
        text-align: center;
  }

  .menu-icon {
        display: block;
  }

}




